* {
  margin: 0;
  padding: 0;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
}

.wrapper {
  min-height: 100%;
}

.head {
  width: 100%;
  opacity: 1;
  height: 50px;
  z-index: 5;
  flex-direction: column;
  display: flex;
  position: fixed;
  top: 0;
}

.head__content {
  width: 100%;
  height: 50px;
  opacity: 1;
  background: linear-gradient(#0a0a0a, #141414 50%);
  justify-content: space-around;
  align-items: center;
  transition: all .25s linear;
  display: flex;
  position: absolute;
  top: -200%;
}

.head__content.visible {
  top: 0;
}

.head__call-area {
  height: 50px;
  width: 100%;
  display: flex;
}

.logo {
  width: 20px;
  height: 20px;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 50%;
  padding: 10px;
  overflow: hidden;
}

.logo__link {
  text-decoration: none;
}

.logo__img {
  width: 25px;
  height: 25px;
}

.menu {
  width: 80%;
}

.menu__list {
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  display: flex;
}

.menu__item {
  border-radius: 5px;
  transition: all 1s;
}

.menu__item.active {
  box-shadow: 0 0 5px #fffdfd;
}

.menu__link {
  color: #faebd7;
  margin: 0 5px;
  text-decoration: none;
  display: block;
}

.main {
  width: 100%;
  flex-direction: row-reverse;
  display: flex;
}

.content {
  width: 100%;
}

.content__block {
  height: auto;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.hi-block {
  text-align: center;
  width: 100%;
  height: 100vh;
  background: #43457c;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.hi-block__title {
  font-size: calc(30px + (80-30) * ((100vw) / (1280) ) );
}

.decor {
  height: 2px;
  width: 85%;
  background: #000;
}

.hi-block__text {
  font-size: calc(15px + (50-15) * ((100vw) / (1280) ) );
}

.text-block {
  height: 80%;
  width: 80%;
  justify-content: space-around;
  align-items: flex-start;
  display: flex;
}

.text-block__container {
  width: 40%;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.text-block__title {
  margin-bottom: 5px;
}

.text-block__text {
  margin-top: 5px;
}

.text-block__decor {
  flex-direction: column;
  align-self: stretch;
  align-items: center;
  display: flex;
}

.text-block__decor_circle {
  width: 5px;
  height: 5px;
  border: 2px solid #000;
  border-radius: 50%;
}

.text-block__decor_line {
  height: 97%;
  width: 2px;
  background: #000;
}

@media (max-width: 767px) {
  .text-block {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }

  .text-block__container {
    width: 80%;
  }

  .text-block__decor {
    width: 80%;
    flex-direction: row;
    justify-content: center;
    align-self: center;
    margin: 20px 0;
    display: flex;
  }

  .text-block__decor_line {
    width: 97%;
    height: 2px;
  }
}

.sidebar {
  width: 2%;
  background: #3b3939;
  position: sticky;
  top: 7vh;
}

.footer {
  width: 100%;
  color: #faebd7;
  height: 5vh;
  z-index: 5;
  background: #1b1e20;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  bottom: 0;
}

.footer__text {
  margin-bottom: -5px;
  font-size: 12px;
}

.footer__license {
  font-size: 8px;
}

